.main-header {
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    position: relative;
    flex-wrap: wrap;
}


  
@media (min-width: $tablet-width) {
  .main-header {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: $desktop-width) {
  .main-header {
    width: 1360px;
  }
}