.main-container-catalog__sort-wrapper {
    display: flex;
    justify-content: center;
}
.main-container-catalog__sort-title {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
}
.main-container-catalog__sort-select {
    border: none;
    color: #7859CF;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    padding:  6px 2px;
    background-color: #F8F8F8;
    font-family: "Montserrat", "Helvetica", sans-serif;
    position: relative;
    bottom: 5px;
}
.main-container-catalog__cards {
    margin: 0 auto;
    padding: 0;
    list-style: none;
    width: 280px;
}
.main-container-catalog__card {
    width: 280px;
    padding: 20px;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 4px;
}
.main-container-catalog__card:not(:last-child) {
    margin-bottom: 20px;
}
.main-container-catalog__card-image {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
}
.main-container-catalog__card-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
}
.main-container-catalog__card-description {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
}
.main-container-catalog__card-price {
    font-weight: 600;
    font-size: 20px;
    line-height: 32px;
}
.main-container-catalog__card-price-wrapper {
    display: flex;
    justify-content: space-between;
}
.main-container-catalog__card-button {
    font-family: "Montserrat", "Helvetica", sans-serif;
    border: none;
    background: #7859CF;
    border-radius: 4px;
    color: #fff;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    padding: 8px 20px 8px 46px;
    position: relative;
}
.main-container-catalog__card-button::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 19px;
    width: 20px;
    height: 16px;
    background-image: url("../img/basket.svg");
}
.main-container-catalog__pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 280px;
    margin-top: 40px;
    padding-bottom: 44px;
}
.main-container-catalog__pagination-list {
    display: flex;
    width: 280px;
    justify-content: center;
    margin: 0 ;
    padding: 0;
    list-style: none;
}
.main-container-catalog__pagination-list-item {
    padding: 10px 17px;
    background: #FFFFFF;
    border-radius: 4px;
    box-sizing: border-box;
}
.main-container-catalog__pagination-list-item--current {
    background-color: #F8F8F8;
}
.main-container-catalog__pagination-list-item-link--current {
    color: #333333;
    font-weight: 600;
}
.main-container-catalog__pagination-list-item:not(:last-child) {
    margin-right: 20px;
}
.main-container-catalog__pagination-arrow-left,
.main-container-catalog__pagination-arrow-right {
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    width: 7px;
    height: 10px;
    padding: 15px 17.5px;
    background-color: #FFFFFF;
}
.main-container-catalog__pagination-arrow-right {
    transform: rotate(180deg);
}
.main-container-catalog__pagination-list-item-link {
    text-decoration: none;
}
@media (min-width: $tablet-width) {
    .main-container-catalog__sort-wrapper {
        display: block;
        margin-left: auto;
        width: 300px;
        margin-right: 20px;
        margin-top: 44px;
        margin-bottom: 30px;
    }
    .main-container-catalog__sort-select {
        bottom: 0;
        &:hover {
            background: #F0EBFF;
            border-radius: 4px;
        }
    }
    .main-container-catalog__sort-select option:checked {
        background: #7859CF;
        color: #fff;
        
    }
    .main-container-catalog__cards {
        width: auto;
        display: grid;
        grid-template-columns: repeat(2, 349px);
        justify-content: center;
        gap: 30px;
    }
    .main-container-catalog__card {
        width: auto;
        min-width: 349px;
        min-height: 440px;
        
    }
    .main-container-catalog__card:not(:last-child) {
        margin-bottom: 0;
    }
    .main-container-catalog__card-title {
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 0;
    }
    .main-container-catalog__card-button {
        &:hover {
            background: #9070EC;
        }
        &:active {
            background: #593CAC;
        }
        &:focus {
            background: #9070EC;
        }
        &:disabled {
            background: #BDBDBD;
        }
    }
    .main-container-catalog__pagination {
        margin-top: 70px;
        margin-left: auto;
        margin-right: 53px;
        width: 395px;
        padding-bottom: 90px;
    }
    
    .main-container-catalog__pagination-arrow-left,
    .main-container-catalog__pagination-arrow-right {
        position: relative;
        background-image: none;
        width: 100%;
    }
    .main-container-catalog__pagination-arrow-left::before {
        content: "Влево";
        position: absolute;
        width: 45px;
        height: 24px;
        font-size: 14px;
        line-height: 24px;
        color: #7859CF;
        top: 7px;
    }
    .main-container-catalog__pagination-arrow-right::before {
        content: "Вправо";
        transform: rotate(-180deg);
        position: absolute;
        width: 45px;
        height: 24px;
        font-size: 14px;
        line-height: 24px;
        color: #7859CF;
        top: 8px;
    }
}
@media (min-width: $desktop-width) {
    .main-container-catalog__cards {
        justify-content: start;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
        margin-right: 41px;
    }
    .main-container-catalog__sort-wrapper {
        margin-right: 40px;
        margin-bottom: 30px;
    }
    .main-container-catalog__card {
        width: auto;
        min-width: 317px;
        min-height: 460px;
        
    } 
    .main-container-catalog__card-price-wrapper {
        margin-top: 60px;
    }
}