.main-container-filter {
    background: #F8F8F8;
}
.main-container-filter__title-description {
    color: #7859CF;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding-top: 50px;
}
.main-container-filter__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
}
.main-container-filter__form {
    margin: 0 auto;
    width: 280px;
}

.main-container-filter__slider-wrapper {
    width: 280px;
}
.main-container-filter__slider-title {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 22px;
}
.main-container-filter__slider-line {
    background: #E2E2E2;
    height: 2px;
    position: relative;
    margin-bottom: 35px;
}
.main-container-filter__slider-container {
    background: #9070EC;
    height: 4px;
    width: 90%;
}
.main-container-filter__slider-min,
.main-container-filter__slider-max {
    width: 24px;
    height: 24px;
    border: 2px solid #9070EC;
    border-radius: 50%;
    background: #FFFFFF;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.main-container-filter__price {
    border: none;
    margin: 0;
    padding: 0;
}
.main-container-filter__price-wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 22px;
}
.main-container-filter__price-min,
.main-container-filter__price-max {
    border: 2px solid #9070EC;
    border-radius: 4px;
    width: 114px;
    height: 34px;
    padding-top: 2px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    box-sizing: border-box;
    font-family: "Montserrat", "Helvetica", sans-serif;
    text-align: center;
}
.main-container-filter__price-range {
    width: 8px;
    height: 2px;
    background: #BDBDBD;
    margin: 0 18px;
}
.main-container-filter__available-milk,
.main-container-filter__country-from {
    border: none;
    padding: 0;
}
.main-container-filter__available-milk {
    margin-bottom: 22px;
}

.main-container-filter__available-milk-title,
.main-container-filter__country-from-title {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 20px;
}
.main-container-filter__available-milk-list,
.main-container-filter__country-from-list {
    padding: 0;
    margin: 0;
    list-style: none;
}
.main-container-filter__available-milk-item,
.main-container-filter__country-from-item {
    position: relative;
}
.main-container-filter__available-milk-item:not(:last-child),
.main-container-filter__country-from-item:not(:last-child) {
    margin-bottom: 20px;
}

.main-container-filter__control-radio {
    position: absolute;
    border: 2px solid #7859CF;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: #ffffff;
    left: 0;
    top: -1px;
  
    &:hover {
        background:  rgba(120, 89, 207, 0.25) ;
        box-shadow: inset 0 0px 0px 3px white;
    }
    &:focus {
        background: #e3daff;
        box-shadow: inset 0 0px 0px 3px white;
    }

}
.main-container-filter__available-milk-radio {
    visibility: hidden;
}
.main-container-filter__available-milk-radio[type="radio"]:disabled ~ .main-container-filter__control-radio {
    border: 2px solid #E2E2E2;
    background: #fff;
    &:hover {
        background: #fff;
        box-shadow: none;
    }
}
.main-container-filter__available-milk-radio[type="radio"]:disabled ~ .main-container-filter__available-milk-description {
    color: #BDBDBD;
}

.main-container-filter__available-milk-radio[type="radio"]:checked + .main-container-filter__control-radio::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 18px;
    background-color: #7859CF;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.main-container-filter__available-milk-radio[type="radio"]:disabled ~ .main-container-filter__control-radio::before {
    background-color: #E2E2E2;
}
.main-container-filter__available-milk-description {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin-left: 10px;
}
.main-container-filter__country-from {
    border: none;
    margin-bottom: 35px;
}
.main-container-filter__country-from-checkbox {
    position: absolute;
    margin: 8px 0 0 16px;
    visibility: hidden;   
}
.main-container-filter__country-from-description {
    position: relative;
    bottom: 3px;
}
.main-container-filter__control-checkbox {
    position: relative;
    padding: 5px 0 0 50px;
    line-height: 2.0em;
    display: block;
    
}
.main-container-filter__control-checkbox::before {
    content: "";
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 42px; 
    height: 24px; 
    border-radius: 12px; 
    background: #BDBDBD;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.main-container-filter__control-checkbox::after {
    content: "";
    position: absolute;
    display: block;
    left: 2px;
    top: 0px;
    width: 20px;
    height: 20px; 
    border-radius: 50%;
    background: #fff;
    transform: translateY(10%);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.main-container-filter__country-from-checkbox +.main-container-filter__control-checkbox:hover::before {
    background: rgba(120, 89, 207, 0.25);
    box-shadow: inset 0px 0px 0px 2px#9070EC;
}
.main-container-filter__country-from-checkbox:disabled +.main-container-filter__control-checkbox:hover::before {
    box-shadow: none;
}
.main-container-filter__country-from-checkbox:checked ~.main-container-filter__control-checkbox:hover::after {
    box-shadow: none;
}
.main-container-filter__country-from-checkbox:checked + .main-container-filter__control-checkbox::after {
    margin-left: 18px;
}
.main-container-filter__country-from-checkbox:checked +.main-container-filter__control-checkbox::before {
    background: #7859CF;
}
.main-container-filter__country-from-description {
    margin-left: 51px;
}
.main-container-filter__country-from-checkbox:disabled + .main-container-filter__control-checkbox::before{
    background: #E2E2E2;
}
.main-container-filter__country-from-checkbox:disabled:checked + .main-container-filter__control-checkbox::after {
    background: #BDBDBD;
}
.main-container-filter__country-from-checkbox:disabled + .main-container-filter__control-checkbox::after {
    background: #F8F8F8;
}
.main-container-filter__country-from-checkbox:disabled ~ .main-container-filter__country-from-description {
    color: #BDBDBD;
}
.main-container-filter__submit-button {
    border: none;
    background: #7859CF;
    border-radius: 4px;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 13px;
    line-height: 24px;
    padding: 10px 14px;
    letter-spacing: 0.04em;
    font-family: "Montserrat", "Helvetica", sans-serif;
    margin: 0 auto;
    display: block;
    margin-bottom: 35px;
}
.main-container-filter__reset-button {
    color: #7859CF;
    border: none;
    background-color: #fff;
    padding: 10px 16px;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.04em;
    margin: 0 auto;
    display: block;
    margin-bottom: 42px;
    border-radius: 4px;
}
@media (min-width: $tablet-width) {
    .main-container-filter__title-description {
        margin: 0;
        padding-top: 80px;
    }
    .main-container-filter__title {
        font-weight: 700;
        font-size: 42px;
        line-height: 64px;
        margin: 0;
        margin-bottom: 25px;
    }
    .main-container-filter__form {
        width: auto;
    }
    .main-container-filter__form-field {
        display: flex;
        width: auto;
        justify-content: space-around;
        padding: 0;
    }
    .main-container-filter__button-wrapper {
        display: flex;
        width: 315px;
        margin: 0 auto;
        align-items: center;
        justify-content: space-between;
    }
    .main-container-filter__slider-container--disabled {
        background: #BDBDBD;
    }
    .main-container-filter__slider-max,
    .main-container-filter__slider-min {
        
        &:hover {
            background: #e3daff;
            box-shadow: inset 0 0px 0px 2px rgba(255, 255, 255, 1);
        }
        &:active {
            background: #7859CF;
            box-shadow: inset 0 0px 0px 2px rgba(255, 255, 255, 1);
        }
        &--disabled {
            border: 2px solid #BDBDBD;
        }
    }
    .main-container-filter__price-min,
    .main-container-filter__price-max {
        &:focus {
            border: 2px solid #593CAC;
        }
        &:disabled {
            border: 2px solid #E2E2E2;
        }
        
    }
    .main-container-filter__submit-button,
    .main-container-filter__reset-button {
        margin: 0;
    }
    .main-container-filter__submit-button {
        padding: 8px 4px;
    }
}
@media (min-width: $desktop-width) {
    .main-container-filter__form {
        float: left;
        margin-left: 40px;
        margin-right: 30px;
    }
    .main-container-filter__form-field {
        display: block;
    }
    .main-container-filter__button-wrapper {
        margin: 0;
    }
    .main-container-filter__slider-wrapper {
        width: auto;
    }
    .main-container-filter__price-min,
    .main-container-filter__price-max {
        width: 134px;
        
    }
}