.main-header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

}
.main-header-wrapper__link {
    margin-left: 20px;
}
.main-header-wrapper__list-icon{
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
}
.main-header-wrapper__item-icon-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    &:active{
        background: #F0EBFF;
    }
}
.main-header-wrapper__toggle{
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    background-color: #fff;
}
.main-header-wrapper__toggle--closed {
    background-image: url("../img/menu.svg");
    &:active {
        background-color: #F0EBFF;
    }
    
}
.main-header-wrapper__toggle--opened {
    background-image: url("../img/close-mark.svg");
}
.main-header-wrapper__icon-description {
    display: none;
}
@media (min-width: $tablet-width) {
    .main-header-wrapper__logo {
      width: 82px;
      height: 36px;
    }
    .main-header-wrapper__item-icon-link {
        &:hover{
            background: #F0EBFF;
        }
        
    }
    .main-header-wrapper__toggle {
        display: none;
    }
    .main-header-wrapper__item-icon:last-child {
        margin-right: 20px;
    }  
}
@media (min-width: $desktop-width) {
    .main-header-wrapper__logo {
        width: 170px;
        height: 36px;
    }
    .main-header-wrapper__link {
        position: relative;
    }
    .main-header-wrapper__link::after {
        content: 'Интернет-магазин кофейных напитков';
        width: 126px;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        position: absolute;
        right: -146px;
        top: 1px;
    }
    .main-header-wrapper__item-icon-link {
        width: auto;
        height: auto;
        padding: 32px 20px;
        text-decoration: none;
        color:#333333;
    }
    .main-header-wrapper__icon-description {
        margin-left: 8px;
        display: block;
    }
}