.footer__map {
    height: 420px;
    
}
.footer__map-background-image {
    width: 100%;
    height: 420px;
}
@media (min-width: $tablet-width) {
    .footer__map {
        height: 480px;
        
    }
    .footer__map-background-image {
        width: 100%;
        height: 480px;
    }
}
@media (min-width: $desktop-width) {
    .footer__map {
        height: 540px;
        
    }
    .footer__map-background-image {
        width: 100%;
        height: 540px;
    }
}