
.main-container-promo__item--flat-white {
    background-color: #F3EBE1;
}
.main-container-promo__item--lavander {
    background-color: #EAE6FC;
}
.main-container-promo__item--espresso {
    background: #E5E6E8;
}
.main-container-promo__image-description--desktop {
    display: none;
}
.main-container-promo__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
}
.main-container-promo__image-wrapper {
    margin-top: 20px;
}
.main-container-promo__image-description {
    text-align: center;
    text-transform: uppercase;
    color: #7859CF;
    font-weight: 700;
    font-size: 12px;
    margin: 10px 0;
}
.main-container-promo__description br {
    display: none;
}
.main-container-promo__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    text-align: center;
    margin: 0;
}
.main-container-promo__description {
    text-align: center;
    padding: 0 20px;
    font-weight: 400;
    font-size: 14px;
}
.main-container-promo__old-price {
    color: #7859CF;
    font-weight: 400;
    font-size: 16px;
}
.main-container-promo__new-price {
    font-weight: 600;
    font-size: 20px;
}
.main-container-promo__order-button {
    background: #7859CF;
    border-radius: 4px;
    border: none;
    font-weight: 400;
    font-size: 16px;
    color: #ffffff;
    padding: 8px 20px;
    margin-left: 20px;
    font-family: "Montserrat", "Helvetica", sans-serif;
}

@media (min-width: $tablet-width) {
    .main-container-promo__item {
        position: relative;
        padding-bottom: 308px;
        margin-bottom: 188px;
    }
    .main-container-promo__image {
        width: 476px;
        height: 476px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: -185px;
    }
    .main-container-promo__title {
        font-size: 56px;
        line-height: 80px;
    }
    .main-container-promo__description {
        padding: 0 80px;
        font-size: 20px;
        margin-bottom: 25px;
    }
    .main-container-promo__order-button {
        &:hover {
            background: #9070EC;
        }
        
        &:active {
            background: #593CAC;
        }
        
        &:disabled {
            background: #BDBDBD;
        }
        &:focus {
            background: #9070EC;
        }
    }
    .main-container-promo__image-description {
        margin-top: 40px;
        margin-bottom: 0;
    }
}
@media (min-width: $desktop-width) {
    .main-container-promo__item {
        display: block;
        margin-bottom: 0;
        padding-bottom: 80px;
    }
    .main-container-promo__image {
        width: 600px;
        height: 600px;
        position:static;
        float: left;
        transform: none;
        margin-left: 105px;
    }
    .main-container-promo__image-description {
        text-align: start;
        
    }
    .main-container-promo__title {
        text-align: start;
        line-height: 80px;
    }
    .main-container-promo__description {
        text-align: start;
        line-height: 32px;
    }
    .main-container-promo__description br {
        display: block;
    }
    .main-container-promo__image-description {
        margin: 0;
    }
    .main-container-promo__image-description--mobile {
        display: none;
    }
    
    .main-container-promo__image-description--desktop,
    .main-container-promo__title,
    .main-container-promo__description,
    .main-container-promo__price {
        position: relative;
        left: 30px;
    }
    .main-container-promo__image-description--desktop {
        display: block;
        margin-top: 170px;
        left: 34px;
    }
}