.main-container-advantage {
    padding-bottom: 28px;
}
.main-container-advantage__title-description {
    font-weight: 700;
    font-size: 12px;
    color: #7859CF;
    text-transform: uppercase;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 0;
}
.main-container-advantage__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    margin: 0;
}
.main-container-advantage__list {
    margin: 0;
    padding: 0;
    margin-top: 36px;
}
.main-container-advantage__item {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 18px;
}
.main-container-advantage__item-title {
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    margin: 0;
    margin-top: 10px;
}
.main-container-advantage__item-description {
    font-weight: 400;
    font-size: 14px;
    text-align: center;
    margin-top: 7px;
}
@media (min-width: $tablet-width) {
    .main-container-advantage {
        padding-bottom: 100px;
    }
    .main-container-advantage__title {
        font-size: 42px;
        line-height: 64px;
    }
    .main-container-advantage__list {
        display: grid;
        grid-template-columns: repeat(2, 340px);
        
        justify-content: space-around;
    }
    .main-container-advantage__item {
        display: block;
        width: 339px;
        
    }
    .main-container-advantage__item:nth-child(odd) {
        margin-left: 30px;
    }
    .main-container-advantage__icon-wrapper {
        height: 100%;
        float: left;
        margin-right: 20px;
    }
    .main-container-advantage__icon {
        float: left;
    }
    .main-container-advantage__item-title {
        text-align: start;
        font-size: 24px;
    }
    .main-container-advantage__item-description {
        text-align: start;
        font-size: 16px;
        line-height: 24px;
        margin-top: 17px;
    }
}
@media (min-width: $desktop-width) {
    .main-container-advantage {
        padding-bottom: 96px;
    }
    .main-container-advantage__title-description {
        margin-top: 100px;
        
    }
    .main-container-advantage__title {
        margin-bottom: 80px;
    }
    .main-container-advantage__list {
        display: grid;
        grid-template-columns: repeat(4, 317px);
        
        justify-content: space-around;
    }
    .main-container-advantage__item {
        display: flex;
        width: 339px;
        align-items: center;
    }
    .main-container-advantage__item:nth-child(odd) {
        margin-left: 0;
    }
    .main-container-advantage__icon-wrapper {
        margin-bottom: 16px;
        margin-right: 0;
    }
    .main-container-advantage__icon {
        float: left;
    }
    .main-container-advantage__item-title {
        text-align: center;
        font-size: 24px;
        margin-bottom: 10px;
    }
    .main-container-advantage__item-description {
        text-align: center;
        font-size: 16px;
        line-height: 24px;
    }
}
