.swiper-button-prev {
    left: 10px;
}
.swiper-button-next {
    right: 10px;
}
.swiper-button-prev,
.swiper-button-next {
    top: 50%;
}
.swiper-pagination {
    display: none;
}
.swiper-button-prev,
.swiper-button-next {
    transform: scale(0.4);
}
@media( min-width:$tablet-width) {
    .swiper-button-prev {
        left: -24px;
    }
    .swiper-button-next {
        right: -22px;
    }
    .swiper-button-prev,
    .swiper-button-next {
        min-width: 80px;
        min-height: 300%;
        top: -100%;
    }
    

    
    
    .swiper-button-prev {
        &:hover {
            background: linear-gradient(90deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
        }
        &:active {
            background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
        }
    }
    .swiper-button-next {
        &:hover {
            background: linear-gradient(90deg,rgba(255, 255, 255, 0) 0%, #FFFFFF 100% );
        }
        &:active {
            background: linear-gradient(90deg, rgba(120, 89, 207, 0) 0%,rgba(120, 89, 207, 0.25) 100%);
        }
        &:disabled {
            background: #BDBDBD;
        }
    }
      
    
}
@media (min-width:$desktop-width) {
    .swiper-pagination {
        display: block;
    }
}