.main-nav {
    width: 100%;
}
.main-nav--closed {
    display: none;
}
.main-nav--open {
    display: block;
}
.main-nav--nojs {
    display: block;
}
.main-nav__list {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    margin: 0;
    padding: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 400;
    width: 100%;
    background-color: #fff;
}
.main-nav__item {
    min-height: 57px;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    border-top: 1px solid #F0EBFF;
}

.main-nav__item:last-child {
    border-bottom: 1px solid #F0EBFF;
 }

.main-nav__link {
    width: 100%;
    height: 59px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #333333;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    &:active {
        background: #F0EBFF;
    }
}
.main-nav__link--current {
    color: #7859CF;
}
.main-nav--tablet-desktop {
    display: none;
}



@media (min-width: $tablet-width) {
    .main-nav {
        width: auto;
    }
    .main-nav__list {
        position: relative;
        flex-direction: row;
        margin-left: 100px;
    }
    .main-nav__item:not(:last-child) {
        margin-right: 20px;
    }
    .main-nav__item {
        border: none;
        min-height: auto;
        
    }
    .main-nav__item--current {
        position: relative;
    }
    .main-nav__item--current::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #7859CF;
        bottom: 0;
    }
    .main-nav__item:last-child {
        border: none;
    }
    .main-nav__link {
        height: 16px;
        padding: 32px 20px;
        &:hover {
            color: #7859CF;
        }
        &:focus {
            color: #7859CF;
        }
        &:active {
            background: #F0EBFF;
        }
    }
    .main-nav--mobile {
        display: none;
    }
    .main-nav--tablet-desktop {
        display: block;
    }
}
@media (min-width: $desktop-width) {
    .main-nav__item--current::before {
        bottom: -2px;
    }
}