.footer-container__social {
    display: flex;
    padding: 20px;
    justify-content: space-between;
}
.footer-container__social-icons {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    width: 160px;
    align-items: center;
    justify-content: space-around;
}
.footer-container__navigation-wrapper {
    display: flex;
    flex-direction: column;
}
.footer-container__social-icons-item-link {
    width: 20px;
    height: 20px;
}

.footer-container__navigation-container {
    padding-bottom: 20px;
    border-bottom: 1px solid #F0EBFF;
    margin-bottom: 6px;
    padding-left: 20px;
}
.footer-container__navigation-container:first-child {
    border-top: 1px solid #F0EBFF;
}
.footer-container__navigation-container-title,
.footer-container__navigation-location-title {
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    color: #7859CF;
    letter-spacing: 0.02em;  
}
.footer-container__navigation-location {
    padding-left: 20px;
    border-bottom: 1px solid #F0EBFF;
    padding-bottom: 20px;
}
.footer-container__navigation-list {
    margin: 0;
    padding: 0;
    list-style: none;
}
.footer-container__navigation-item:not(:last-child) {
    margin-bottom: 12px;
}
.footer-container__navigation-item-link {
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-decoration: none;
    text-transform: uppercase;
    color: #333333;
}
.footer-container__social-icon {
    fill: #BDBDBD;
}
.footer-container__navigation-item-address {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-decoration: none;
    color: #333333;
    margin-left: 19px;
    position: relative;
}
.footer-container__navigation-item-address--pin::before,
.footer-container__navigation-item-address--phone::before {
    background-image: url("../img/pin.svg");
    position: absolute;
    content: "";
    left: -18px;
    width: 13px;
    height: 16px;
    top: 1px;
}
.footer-container__navigation-item-address--phone::before {
    background-image: url("../img/phone.svg");
}
.footer-container__design {
    width: 280px;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    padding-bottom: 30px;
}
.footer-container__design-link {
    color: #BDBDBD;
    position: relative;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    text-decoration: none;
}
.footer-container__design-link::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 16px;
    background-image: url("../img/html-academy.svg");
    right: -15px;
}
@media (min-width: $tablet-width) {
    .footer-container__logo {
        width: 170px;
        height: 36px;
    }
    .footer-container__social-icons {
        width: 160px;
        justify-content: space-between;
    }
    .footer-container__navigation-item-link,
    .footer-container__navigation-item-address {
        &:hover {
            color: #9070EC;
        }
        &:focus {
            color: #9070EC;
        }
        &:active {
            color: #593CAC;
        }
    }
    .footer-container__social-icons-item {
        padding: 0 8px;
    }
    .footer-container__social-icon {
        
        &:hover {
            fill: #7859CF;
        }
        &:focus {
            fill: #7859CF;
        }
    }
    .footer-container__navigation-wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .footer-container__navigation-container,
    .footer-container__navigation-location,
    .footer-container__navigation-container:first-child {
        border: none;
    }
    .footer-container__navigation-location {
        width: 223px;
    }
    .footer-container__design {
        margin-top: 10px ;
        padding-bottom: 40px;
    }
}
@media (min-width: $desktop-width) {
    .footer-container {
        padding-top: 45px;
    }
    .footer-container__social {
        display: block;
        float: left;
        min-height: 151px;
        box-sizing: border-box;
        position: relative;
        padding: 0;
        margin-left: 40px;
    }
    .footer-container__social::before {
        content: "Интернет-магазин кофейных напитков";
        position: absolute;
        width: 318px;
        height: 20px;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #333333;
        top: 45px;
    }
    .footer-container__social-icons {
        margin-top: 70px;
    }
    .footer-container__navigation-wrapper {
        padding-left: 15px;
        justify-content: space-around;
    }
}